.App {
  font-family: 'Times New Roman', Times, serif;
  text-align: left;
  padding: 20px;
}

a{
  color: black
}
.button {
  font-family: 'Times New Roman', Times, serif;
}


h3 {
  border-style: double;
  border-color: black;
  padding: 5px;
  border-radius: 5px;
  display: inline-block;
}

h1 {
  background-color: #282c34;
  color: white;
  font-size: 3em;
  margin: 0;
  border-style: double;
  border-color: black;
  padding: 5px;
  border-radius: 5px;
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
